import React from "react";
import "./inputType-style.scss";
const InputType = ({ tag, type = "text", name, placeholder, className }) => {
  const Tag = `${tag}`;
  return (
    <div className="inputFormGroup">
      <Tag type={type} className={className} name={name} placeholder={placeholder} />
    </div>
  );
};

export default InputType;
